body {
font-family:Arial,Helvetica,sans-serif;
background-image: url(data:image/jpg;base64,);
    backdrop-filter: blur(7px);
    background-size: cover;
    background-attachment: fixed;
    margin: 20px;
}

#root {
    border-radius: 8px;
    overflow: hidden;
}

.steps-navigation {
    background-color: #1e1e1e;
    color: #fff;
}

.step-form {
    margin: 0 15px;
}

#steps-list {
    overflow: scroll;
}

#steps-list,
#blueprint-code {
    height: calc(100vh - 140px);
}